<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`FundsTransferType${dataLayout}`" :key="Date.now()"/>
  </div>
</template>

<script>
import FundsTransferTypeListLayout from '@/views/ft/funds-transfer-type/funds-transfer-type-browse/FundsTransferTypeListLayout'
import FundsTransferTypeTreeLayout from '@/views/ft/funds-transfer-type/funds-transfer-type-browse/FundsTransferTypeTreeLayout'

export default {
  name: 'funds-transfer-type-browse',
  components: {
    FundsTransferTypeListLayout,
    FundsTransferTypeTreeLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
